body {
  background-color: #eee;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

img {
  margin: auto;
  display: flex;
  border: 1px solid rgb(180, 180, 180);
  width: 22%;
  border-radius: 15px;
}

// .title {
//   margin: auto;
//   text-align: center;
// }

.mainContainer {
  display: flex;
}

.contract, .existing-contracts {
  margin: 20px;
  width: 50%;
  padding: 25px;
  border: 1px solid rgb(180, 180, 180);
  background-color: #fff;
  border-radius: 15px;
}

h1 {
  margin: 10px;
  padding: 10px;
  border-bottom: 1px solid rgb(226, 226, 226);
  font-size: 22px;
}

label {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  color: #aaa;
  margin: 20px;
  font-size: 14px;
}

input[type="text"] {
  padding: 6px;
  font-size: 14px;
}

.close {
  background-color: rgb(218, 35, 35);
  color: white;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  margin: 20px;
  padding: 9px 25px;
  font-size: 14px;
  letter-spacing: 1px;
  cursor: pointer;
}

.button {
  background-color: #062179;
  color: white;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  margin: 20px;
  padding: 9px 25px;
  font-size: 14px;
  letter-spacing: 1px;
  cursor: pointer;
}

#withdraw{
  background-color: rgb(19, 161, 43);
}

.existing-contract ul {
  padding: 10px;
  margin: 20px;
  background-color: #eee;
}

.existing-contract ul > li {
  list-style: none;
  display: flex;
  margin: 10px;
}

.existing-contract ul > li > div {
  margin: 0 5px;
}

.existing-contract ul > li > :first-child {
  font-weight: bold;
}

.instructions{
  text-align: center;
  list-style-position: inside;
  margin: auto;
  width: 65%;
  border: 1px solid #ddd;
  background-color: #fff;
  border-radius: 15px;
  padding-right: 20px;
}

.instructions li {
  margin: 10px 0;
  text-align: left;
}

#addrContainer {
  padding-left: 20px;
}